<template>
  <div class="main_inex_change_info">
    <mt-field class="mt_item" disabled label="">
      <div class="tx_div_wapper common_back hasBg" id="change_nick" @click="changeHeadImg"></div>
    </mt-field>
    <!--  -->
    <mt-field label="" placeholder="" v-model="nickForm.nickName"></mt-field>
    <!--  -->
    <mt-field class="mt_item" disabled label="">
      <div class="tx_div_wapper common_back hasBg" id="back_bk" @click="changeBkImg"></div>
    </mt-field>
    <!--  -->
    <mt-field class="mt_item" disabled label="">
      <mt-switch v-model="nickForm.ifNoRead"></mt-switch>
    </mt-field>
    <span v-show="nickForm.ifNoRead">
      <!--  -->
      <mt-field class="" disabled label="">
        <div class="tx_div_wapper common_back hasBg" id="no_read_img" @click="changeReadImg"></div>
      </mt-field>
      <!--  -->
      <mt-field class="" type="number" label="" placeholder="" v-model="nickForm.noReadNum">
      </mt-field>
    </span>
    <!--  -->
    <mt-field class="mt_item" disabled label="">
      <mt-switch v-model="nickForm.ifNewDt"></mt-switch>
    </mt-field>
    <mt-field v-show="nickForm.ifNewDt" class="" label="">
      <div class="tx_div_wapper common_back hasBg" id="fir_dt_img" @click="changeFriImg"></div>
    </mt-field>
    <!--  -->
    <common-upload ref="loadImg" :fatherClass="'changenick'" :ifM="setUploadM" @setImg="setImg" :needWidHei="true"></common-upload>
    <!--  -->
    <mt-popup v-model="popupVisible14" class="app_pop_wapper">
      <div class="app_pop_inner">
        <div class="pop_item" @click="addImg('local')"></div>
        <div class="pop_item" @click="addImg('sj1')">1</div>
      </div>
    </mt-popup>
    <!--  -->
    <div class="inner_make_btn mt_item">
      <mt-button type="primary" size="small" @click="postForm"></mt-button>
      <!-- <mt-button size="small" type="danger" @click="delForm"></mt-button> -->
      <mt-button type="primary" plain size="small" @click="backBtn"></mt-button>
    </div>
    <div v-if="!canPost" class="tips_text"></div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
import { returnImg } from "@/utils/makeJson.js"
import commonUpload from '@/components/commonUpload2'

export default {
  components: {
    commonUpload
  },
  data() {
    return {
      popupVisible14: false,
      setUploadM: false,
      doubleChoseType: 'headImg',
      canPost: true,
      nickForm: {
        yourHeaderImg: '', //
        nickName: '', //
        bkImg: '', //
        ifNoRead: false, //
        noReadImg: '', //
        noReadNum: 1, //
        ifNewDt: false, //
        firNewImg: '', //
      }
    }
  },
  mounted() {

  },
  methods: {
    checkForm() {
      if (!this.nickForm.yourHeaderImg || !this.nickForm.nickName || !this.nickForm.bkImg) {
        // 
        this.canPost = false
      } else {
        this.canPost = true
      }
      return this.canPost
    },
    postForm() {
      if (!this.checkForm()) return
      // 
      this.$emit('nickInfo', this.nickForm)
      // 
      this.backBtn()
    },
    delForm() {},
    backBtn() {
      this.$emit('close')
    },
    changeHeadImg() {
      this.doubleChoseType = 'headImg'
      // 
      this.popupVisible14 = true
    },
    changeBkImg() {
      // 
      this.controlChange('bkImg', false)
    },
    changeReadImg() {
      this.doubleChoseType = 'noReadImg'
      // 
      this.popupVisible14 = true
    },
    changeFriImg() {
      // 
      this.doubleChoseType = 'friDt'
      // 
      this.popupVisible14 = true
    },
    setImg(urls, wid, hei) {
      // 
      console.log(this.whiosGetImg)
      switch (this.whiosGetImg) {
        case 'headImg':
          this.nickForm.yourHeaderImg = urls[0]
          // 
          $("#change_nick").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;
        case 'bkImg':
          // 
          this.nickForm.bkImg = urls[0]
          $("#back_bk").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          // 
          this.setBkImgWid(wid, hei)
          break;
        case 'noReadImg':
          // 
          this.nickForm.noReadImg = urls[0]
          $("#no_read_img").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;
        case 'friDt':
          // 
          this.nickForm.firNewImg = urls[0]
          $("#fir_dt_img").css({
            'background-image': 'url(' + urls[0] + ')'
          })
          break;


      }
    },
    setBkImgWid(wid, hei) {
      // 
      console.log($('.top_fake_bk'))
      if (wid && hei) {
        if (wid > hei) {
          console.log(' >,100%')
          // >,100%
          $('.top_fake_bk').css({
            'height': '100%',
            'width': 'auto'
          })
        } else if (hei > wid) {
          // <,100%
          console.log('<,100%')
          $('.top_fake_bk').css({
            'width': '100%',
            'height': 'auto'
          })
        } else {
          // 
          $('.top_fake_bk').css({
            'width': '100%',
            'height': 'auto'
          })
        }
      }
    },
    controlChange(whios, ifM) {
      this.whiosGetImg = whios
      // 
      this.setUploadM = ifM
      // ,
      setTimeout(() => {
        this.$refs.loadImg.actions()
      }, 100)
    },
    addImg(type) {
      // 
      switch (type) {
        case 'local':
          // 
          this.controlChange(this.doubleChoseType, false)
          break;
        case 'sj1':
          // 1
          this.whiosGetImg = this.doubleChoseType //
          this.setImg(returnImg(1, 0, 300))
          break;
      }
      this.popupVisible14 = false
    }
  },
  watch: {}
}

</script>
<style scoped lang="scss">
.main_inex_change_info {
  background-color: #eae9eb;
  width: 100%;
  height: 100%;
  // padding: 20px;
  overflow: auto;
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;

  .tips_text {
    color: red;
    text-align: center;
    font-size: 24px;
  }

  .inner_make_btn {
    // margin-top: 30px;
    padding: 30px 0;
    text-align: center;
    background: #fff;
  }

  .mt_item {
    margin-top: 30px;
  }

  .tx_div_wapper {
    width: 128px;
    height: 128px;

    &.hasBg {
      background-image: url('../../../assets/nodoby.jpg')
    }
  }
}

</style>



