<template>
  <div class="all_wapper_mIndex_wapper">
    <div v-if="!showDetail" class="makeInfo_wapper common_back">
      <div class="inner_index_content">
        <!--  -->
        <mt-field class="tx_wapper" disabled label="" placeholder="" v-model="badInp">
          <div class="tx_div_wapper common_back" :class="formInline.yourHeaderImg?'':'hasBg'" id="tx_div_wapper_mi" @click="changeHeadImg" :style="{backgroundImage: 'url(' + (formInline.yourHeaderImg) + ')'}"></div>
        </mt-field>
        <!--  -->
        <mt-field label="" placeholder="" v-model="formInline.nickName"></mt-field>
        <!-- () -->
        <mt-field disabled label="()" placeholder="" v-model="badInp">
          <!--  -->
          <div class="inner_no_padding">
            <mt-radio title="" v-model="formInline.contentStyle" :options="['','+']">
            </mt-radio>
          </div>
        </mt-field>
        <!--  -->
        <div class="for_example_inner">
          <span></span>
          <!--  -->
          <template>
            <!--  -->
            <template v-if="formInline.contentStyle===''">
              <div class="inner_show_example common_back"></div>
            </template>
            <!--  -->
            <div v-else class="inner_show_example_other_all common_back"></div>
          </template>
        </div>
        <!--  end-->
        <!--  -->
        <template v-if="formInline.contentStyle===''">
          <!--  -->
          <mt-field type="textarea" label="" placeholder="" v-model="formInline.realContent" placeholder="" rows="4"></mt-field>
          <!--  -->
          <mt-field key="pyqimg" disabled label="" placeholder="" v-model="badInp" rows="4">
            <div class="xt_flex app_wapper_box">
              <!--  -->
              <template v-for="(item,index) in formInline.realImgs">
                <div class="show_imgs common_back" :key="item+index" :style="{backgroundImage: 'url(' + item+ ')'}" @click="delImg(item,index)">
                </div>
              </template>
              <div class="nr_div_wapper common_back" id="nr_div_wapper" @click="changeContentImg"></div>
            </div>
          </mt-field>
        </template>
        <!--   end -->
        <!--  -->
        <template v-else>
          <!--  -->
          <mt-field type="textarea" label="" placeholder="" v-model="formInline.realContent" placeholder="" rows="4"></mt-field>
          <mt-field key="sltimg" disabled label="" v-model="badInp" class="tx_wapper">
            <div class="link_div_wapper common_back" id="link_div_wapper_p" @click="changeLinkImg" :class="formInline.smallImg?'':'hasBg'" :style="{backgroundImage: 'url(' + (formInline.smallImg) + ')'}"></div>
          </mt-field>
          <mt-field label="" placeholder="" v-model="formInline.smallContent" placeholder="">
          </mt-field>
        </template>
        <!--  end -->
        <!--  -->
        <mt-field label="" disabled placeholder="">
          <div @click="getSetTime" v-if="formInline.sendTime" class="fake_show_time">{{formInline.sendTime}}</div>
          <span @click="getSetTime" class="fake_inp_pla" v-else></span>
        </mt-field>
        <!-- end -->
        <mt-field class="diy_inp" label="" :disableClear="true" placeholder=":" v-model="formInline.localtion">
          <mt-button type="primary" class="add_tbns" size="small" @click="addFgDeatail"> </mt-button>
        </mt-field>
        <!--  -->
        <mt-field class="" disabled label="">
          <mt-switch v-model="formInline.ifSelf"></mt-switch>
        </mt-field>
        <!--  -->
        <div class="inner_make_btn">
          <mt-button type="primary" size="small" @click="postForm"></mt-button>
          <mt-button size="small" type="danger" @click="delForm"></mt-button>
          <mt-button type="primary" size="small" @click="backBtn"></mt-button>
        </div>
      </div>
    </div>
    <!--  -->
    <common-upload ref="loadImg" key="makeInfo" :fatherClass="'mainIndexMakeInfo'" :ifM="setUploadM" @setImg="setImg"></common-upload>
    <!--  -->
    <mt-popup v-model="popupVisible8" class="app_pop_wapper">
      <div class="app_pop_inner">
        <div class="pop_item" @click="addImg('local')"></div>
        <div class="pop_item" @click="addImg('sj1')">1</div>
      </div>
    </mt-popup>
    <!-- -->
    <mt-popup v-model="setTimePop" class="app_pop_wapper">
      <chose-time :ifIndex="true" :initTime="formInline.sendTime" @upDateTime="(data)=>{formInline.sendTime=data,setTimePop=false}" @close="setTimePop=false"></chose-time>
    </mt-popup>
    <!--  -->
    <div class="immIndex">
      <img ref="imgzan" width="auto" :src="dialogImageUrl">
    </div>
  </div>
</template>
<script>
import { Toast, MessageBox } from 'mint-ui';
import commBottom from '@/components/commonBottom'
import commonUpload from '@/components/commonUpload2'
import choseTime from '@/components/choseTime'
import "@/styles/makeInfo.scss"
import { returnImg } from "@/utils/makeJson.js"
var _this

import {
  GetDateAndHourStr,
  setLocal,
  getLocal,
  makeRandomCount,
  GetDateAndHourMake
} from '@/utils'
export default {
  // name: 'List',
  components: {
    commBottom,
    commonUpload,
    choseTime
  },
  props: {
    initData: {
      // 
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      setTimePop: false,
      popupVisible8: false,
      dialogImageUrl: '',
      setUploadM: false,
      headImg: '',
      whiosGetImg: '', //
      showDetail: false, //
      badInp: '', //
      makeSelfArrPl: [''], //
      startDate: new Date(),
      formInline: { //
        nickName: '', //
        makeSelfPl: '', //
        yourHeaderImg: '', //
        contentStyle: '', //()
        realContent: '', //
        localtion: '', //
        realImgs: [], //
        realImgsWid: '', //
        realImgsHei: '', //
        smallImg: '', // 
        smallImgLink: 'http://www.baidu.com', // 
        smallContent: '', // 
        sendTime: '', // 
        sendTimeNoformat: new Date().getTime(), // ()
        likeNickName: [], //
        wordsAll: [],
        plMini: false,
        ifSelf: true //
      },
    }
  },
  mounted() {

    _this = this //this
    let getRoute = this.$route
    // 
    this.$nextTick(() => {
      (function(window, document) {
        var stop = document.body.scrollTop; //
        $('input[type="text"],textarea').each(function() {
          $(this).focus(function(e) {
            setTimeout(function() {
              document.activeElement.scrollIntoViewIfNeeded()
            }, 400)
          })
        })
      })(window, document)
    })
  },
  methods: {
    addFgDeatail() {
      // 
      this.formInline.localtion += '  '
      this.$nextTick(() => {
        $('.diy_inp').find('input').focus()
      })
    },
    getSetTime() {
      // 
      this.setTimePop = true
    },
    backBtn() {
      // 
      this.$emit('close')
    },
    addImg(type) {
      // 
      switch (type) {
        case 'local':
          // 
          this.controlChange('headImg', false)
          break;
        case 'sj1':
          // 1
          this.whiosGetImg = 'headImg' //
          this.setImg(returnImg(1, 0, 300))
          break;
      }
      // 
      this.$nextTick(() => {
        this.popupVisible8 = false
      })
    },
    delImg(item, index) {
      // 
      MessageBox.confirm('?').then(action => {
        console.log(item)
        this.formInline.realImgs.splice(index, 1)
      }, cancel => {
        console.log(index)
      });
    },
    setImg(urls) {
      // 
      console.log(this.whiosGetImg)
      switch (this.whiosGetImg) {
        case 'headImg':
          // 
          this.formInline.yourHeaderImg = urls[0]
          break;
        case 'linkImg':
          // 
          this.formInline.smallImg = urls[0]
          break;
        case 'contentImg':
          // 
          this.formInline.realImgs = this.formInline.realImgs ? urls.concat(this.formInline.realImgs) : urls
          // 
          if (this.formInline.realImgs && this.formInline.realImgs.length === 1) {
            this.dialogImageUrl = this.formInline.realImgs[0]
          }
          let t = setTimeout(() => {
            let dom = this.$refs.imgzan
            this.formInline.realImgsWid = dom.width
            this.formInline.realImgsHei = dom.height
          }, 300)
          break;

      }
    },
    changeContentImg() {
      // 
      this.controlChange('contentImg', true)
    },
    changeHeadImg() {
      // 
      this.popupVisible8 = true
    },
    changeLinkImg() {
      this.controlChange('linkImg', false)
    },
    controlChange(whios, ifM) {
      this.whiosGetImg = whios
      // 
      this.setUploadM = ifM
      // ,
      setTimeout(() => {
        this.$refs.loadImg.actions()
      }, 100)
    },
    imgInfo(...data) {
      console.log(data)
      let giveWho = data[0]
      let giveUrl = data[1].url
      this.formInline[giveWho] = giveUrl
    },
    showIndex() {
      // 
      this.showDetail = false
    },

    clearRealImgs() { //
      this.formInline.realImgs = []
    },
    handleConfirm(data) { //
      this.formInline.sendTimeNoformat = data //
      let date = GetDateAndHourMake(data)
      this.formInline.sendTime = date
    },
    uploadBtnOne(refs) { //
      // refsinputref
      // console.log(this.$refs.uploadRef)
      // this.$refs.uploadRef.getStart()
      // this.$refs[refs].click()
    },
    chenckTime() { //
      this.$refs.picker.open()
    },
    addNewPl() { //
      this.makeSelfArrPl.push('')
    },
    delNowContent(index) { //
      this.makeSelfArrPl.splice(index, 1)
    },
    contentChnangeStyle() { //
    },
    postForm() { //
      this.$emit('close')
      this.$emit('backInfoData', this.formInline)
    },
    delForm() { //localStorage
      let _that = this
      let formInlineKeys = Object.keys(this.formInline)
      formInlineKeys.forEach((item, index) => {
        // ,
        if (item !== 'contentStyle') {
          if (typeof _that.formInline[item] === 'array') {
            _that.formInline[item] = []
          } else if (typeof _that.formInline[item] === 'string') {
            _that.formInline[item] = ''
          }
        }
      })
      this.clearRealImgs() //
      localStorage.setItem('makeJson', '')

    },
    rebackData() { //localStorage
      let ccc = getLocal('makeJson')
      if (ccc !== null && ccc !== '' && ccc !== undefined) {
        this.formInline = ccc
      }
    }
  },
  watch: {
    initData: {
      handler(newD, oldD) {
        if (Object.keys(newD).length > 0) {
          console.log(newD)
          this.formInline = newD
        }
      },
      immediate: true
    }
  }
};

</script>
<style scoped lang="scss">
$grey:#ecf3fd; //
$blueFont:#616e91; //
$greyFont:#8a8a8a; // 
$backArea:#eeeeee; //
$greenFont:#69a44a; //
$returnMesBk:#f5f5f5; // 
$returnMesBorder:#dcdcdc; //
$fontSizeCommon:40px;
$fontSizeSmall:35px;
$fontGold:#F5A623; //

.all_wapper_mIndex_wapper {
  .makeInfo_wapper {
    padding: 20px;
    overflow: auto;
    position: fixed;
    padding-bottom: 50px;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-image: url('../../../assets/maldives_wedding-wallpaper-1280x800.jpg');

    .tx_wapper {
      height: 188px;
    }

    .newAdd_pl_wapper {
      text-align: center;
      margin-top: 30px;
    }

    .some_tips {
      text-align: center;
      font-size: $fontSizeSmall;
      background-color: #fff;
      padding: 20px;
      font-weight: bold;
      border-radius: 6px;
      margin-top: 10px;

      a {
        color: $fontGold;
      }
    }

    .inner_make_btn {
      margin-top: 30px;
      text-align: center;
    }

    .for_example_inner {
      color: red;
      margin: 20px 0;

      .inner_show_example {
        width: 100%;
        height: 400px;
        background-image: url('../../../assets/neirong.jpg');
        background-size: contain;
      }

      .pl_show_example {
        width: 100%;
        height: 400px;
        background-image: url('../../../assets/pyq.jpg');
        background-size: contain;
      }

      .inner_show_example_other {
        width: 100%;
        height: 300px;
        background-image: url('../../../assets/lianjie.jpg');
        background-size: contain;
      }

      .inner_show_example_other_all {
        width: 100%;
        height: 300px;
        background-image: url('../../../assets/ljwz.jpg');
        background-size: contain;
      }
    }

    .inner_index_content {
      opacity: .9;

      .make_fh_wapper {}

      .card_inner_text {
        text-align: center;
      }



      .inner_img_header {
        // flex: 0 0 102px;
        vertical-align: bottom;
        margin-right: 20px;
        width: 102px;
        height: 102px;
        display: inline-block;
      }
    }

    .link_div_wapper {
      width: 128px;
      height: 128px;

      &.hasBg {
        background-image: url('../../../assets/fadelink.jpeg') !important;
      }
    }



    .nr_div_wapper {
      width: 128px;
      height: 128px;
      background-image: url('../../../assets/addImg.jpeg')
    }

    .tx_div_wapper {
      width: 128px;
      height: 128px;

      &.hasBg {
        background-image: url('../../../assets/nodoby.jpg') !important;
      }
    }

    .app_wapper_box {
      padding: 20px 0;
      flex-wrap: wrap;

      .show_imgs {
        width: 128px;
        height: 128px;
        margin: 0 15px 15px 0;
      }
    }



    .add_tbns {
      margin-left: 15px;
    }

    .fake_inp_pla {
      color: #757575;
    }


  }

  .immIndex {
    width: 1px;
    height: 1px;
    overflow: hidden;

    img {
      width: auto !important;
    }
  }

  .noShow {
    // display: none;
  }
}

</style>



